.chat-input {
    width: 70%;
    max-width: 1000px;
    margin: 0 auto;
    background-color: #f2f2f2;
    height: 62px;
    border-radius: 3px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-family: sans-serif;
    box-shadow: 1px 7px 13px #adadad;    
    margin-bottom: 10px;
}
textarea {
    width: 84%;
    margin: 0 2%;
    border-radius: 3px;
    border: none;
    outline: none;
    font-size: 16px;
    padding: 5px 20px;
    display: block;    
    resize: none;
    overflow: hidden;
    max-height: 80px;
}
button {
    width: 8%;
    min-width: 70px;
    margin: 0 2%;
    height: 35px;
    border: none;
    box-shadow: 2px 1px 2px gray;
    border-radius: 5px;
    color: white;
    background: #959595;
    font-size: 17px;
}
button.active {
    background: black;
    cursor: pointer;
}